import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';

const Nav_Header = () => {

    const { pathname } = useLocation();
    const [hover, setHover] = useState(null);

    const styles = {
        nav: { width:'100%', display:'flex', justifyContent:'center', background:'var(--cb-white)',
            borderBottom:'2px solid #000', position:'sticky', top:0, zIndex:100 },

        ul: { listStyle:'none', display:'flex', gap:'24px', padding:'14px', margin:0 },

        li: (active, isHover) => ({
            position:'relative', display:'flex', alignItems:'center', gap:'8px',
            padding:'8px 18px', cursor:'pointer',
            transform: isHover ? 'translateY(-4px)' : 'translateY(0)',
            transition:'transform .35s cubic-bezier(.34,1.56,.64,1)'
        }),

        link: (active) => ({
            textDecoration:'none', fontWeight:'600', fontSize:'15px',
            color: active ? 'var(--cb-black)' : '#444',
            transition:'color .3s ease'
        }),

        icon: (active) => ({
            fontSize:'18px',
            transform: active ? 'scale(1.15)' : 'scale(1)',
            transition:'transform .3s ease'
        }),

        underline: (active, isHover) => ({
            position:'absolute', bottom:'0', left:'50%', height:'3px', width: active || isHover ? '100%' : '0%',
            background:'var(--cb-black)', borderRadius:'10px',
            transform:'translateX(-50%)',
            transition:'width .35s ease'
        })
    };

    const menu = [
        { label:'HTML', path:'/contact/html', icon:'📄' },
        { label:'CSS', path:'/css', icon:'🎨' },
        { label:'JavaScript', path:'/javascript', icon:'⚙️' },
        { label:'React + Vite', path:'/react', icon:'⚛️' }
    ];

    return (
        <nav style={styles.nav}>
            <ul style={styles.ul}>
                {menu.map((item, i) => {
                    const active = pathname === item.path;
                    const isHover = hover === i;

                    return (
                        <li key={i}
                            style={styles.li(active, isHover)}
                            onMouseEnter={() => setHover(i)}
                            onMouseLeave={() => setHover(null)}
                        >
                            <span style={styles.icon(active)}>{item.icon}</span>
                            <Link to={item.path} style={styles.link(active)}>{item.label}</Link>
                            <span style={styles.underline(active, isHover)} />
                        </li>
                    );
                })}
            </ul>
        </nav>
    );
};

export default Nav_Header;
