import React from 'react';

const Title_unit = (props) => {
    const {Title_english, Title_khmer} = props;
    return (
        <>
            <h1 style={{textDecoration: 'underline'}}> {Title_english} <span style={{fontFamily: 'khmer'}}> ({Title_khmer}) </span></h1>
        </>
    );
};

export default Title_unit;