import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';

const Main_left = () => {

    const { pathname } = useLocation();
    const [hover, setHover] = useState(null);

    const styles = {
        main: { width:'300px', height:'100%', background:'var(--cb-white)',
            borderRight:'2px solid #000', paddingTop:'10px' },

        ul: { padding:'10px', margin:0 },

        li: (active, isHover) => ({
            listStyle:'none', position:'relative',
            padding:'8px 12px', marginBottom:'6px',
            transform: isHover ? 'translateX(6px)' : 'translateX(0)',
            transition:'all .35s cubic-bezier(.34,1.56,.64,1)',
            background: active ? 'rgba(0,0,0,.05)' : 'transparent',
            cursor:'pointer'
        }),

        link: (active) => ({
            textDecoration:'none', fontSize:'15px', fontWeight: active ? '600' : '500',
            color:'var(--cb-black)', display:'block'
        }),

        bar: (active, isHover) => ({
            position:'absolute', left:0, top:0, height:'100%',
            width: active || isHover ? '4px' : '0',
            background:'var(--cb-black)',
            transition:'width .3s ease'
        })
    };

    const menu = [
        { label:'About HTML', path:'/contact/about_html' },
        { label:'HTML Elements', path:'/contact/html_elements' }
    ];

    return (
        <main-left style={styles.main}>
            <nav>
                <ul style={styles.ul}>
                    {menu.map((item, i) => {
                        const active = pathname === item.path;
                        const isHover = hover === i;

                        return (
                            <li key={i}
                                style={styles.li(active, isHover)}
                                onMouseEnter={() => setHover(i)}
                                onMouseLeave={() => setHover(null)}
                            >
                                <span style={styles.bar(active, isHover)} />
                                <Link to={item.path} title={item.label} style={styles.link(active)}>
                                    {item.label}
                                </Link>
                            </li>
                        );
                    })}
                </ul>
            </nav>
        </main-left>
    );
};

export default Main_left;
