import React from 'react';

import Title_unit from '../props/Title_unit';
import Title_article from '../props/Title_article';
import List_article from '../props/List_article';

const Main_center = () => {

    const styles = {
        main: { width:'100%', height:'calc(100vh - 102px)', display:'flex', justifyContent:'center', padding:'20px',
            background:'linear-gradient(180deg,#0f2027,#203a43,#2c5364)' },

        section: { width:'100%', maxWidth:'1300px', height:'100%', overflowY:'auto', padding:'24px',
            background:'rgba(255,255,255,0.05)', borderRadius:'12px',
            boxShadow:'0 10px 30px rgba(0,0,0,.3)', color:'var(--cb-white)' },

        p: { fontSize:'16px', lineHeight:'1.8', margin:'12px 0 24px', fontFamily:'khmer', opacity:'.95' },

        list: { marginLeft:'20px', marginBottom:'24px', fontFamily: 'khmer' }
    };

    return (
        <main-center style={styles.main}>
            <section style={styles.section}>

                <Title_unit Title_english="About HTML" Title_khmer="អំពី HTML"/>

                <Title_article Title="សេចក្តីផ្តើម" ID="introduction" />
                <p style={styles.p}>
                   HTML មានន័យពេញថា HyperText Markup Language។ វាជាភាសាសម្គាល់ (Markup Language) ដែលត្រូវបានប្រើសម្រាប់បង្កើត និងរៀបចំរចនាសម្ព័ន្ធឯកសារនៅលើបណ្ដាញអ៊ីនធឺណិត (World Wide Web)។ HTML មិនមែនជាភាសាកម្មវិធី (Programming Language) ទេ ប៉ុន្តែវាជាភាសាសម្រាប់កំណត់ថា មាតិកាណាមួយគួរត្រូវបង្ហាញជាចំណងជើង កថាខណ្ឌ តំណភ្ជាប់ រូបភាព ឬធាតុផ្សេងៗ។
                </p>

                <Title_article Title="ប្រវត្តិរបស់ HTML" ID="history_html" />
                <p style={styles.p}>
                    HTML ត្រូវបានបង្កើតឡើងដោយ លោក Tim Berners-Lee។ លោកជាវិទ្យាសាស្ត្រកុំព្យូទ័រជនជាតិអង់គ្លេស ដែលកើតនៅថ្ងៃទី ៨ ខែមិថុនា ឆ្នាំ ១៩៥៥ នៅទីក្រុងឡុងដ៍ ប្រទេសអង់គ្លេស។ ឪពុកម្តាយរបស់លោកក៏ជាវិស្វករកុំព្យូទ័រផងដែរ ដែលបានចូលរួមក្នុងការអភិវឌ្ឍកុំព្យូទ័រដំបូងៗរបស់ពិភពលោក។<br /><br />
                    លោក Tim Berners-Lee បានសិក្សាផ្នែករូបវិទ្យា (Physics) នៅសាកលវិទ្យាល័យ Oxford។ បន្ទាប់ពីបញ្ចប់ការសិក្សា លោកបានធ្វើការនៅអង្គការស្រាវជ្រាវវិទ្យាសាស្ត្រធំមួយឈ្មោះថា CERN (European Organization for Nuclear Research) ដែលមានទីតាំងនៅប្រទេសស្វីស។
                </p>

                <Title_article Title="គោលបំណងនៃការបង្កើត HTML" ID="purpose_html" />
                <p style={styles.p}>
                    នៅសម័យនោះ អ្នកវិទ្យាសាស្ត្រនៅ CERN មានបញ្ហាធំមួយ គឺការចែករំលែកឯកសារ និងព័ត៌មានរវាងគ្នា ពីព្រោះប្រព័ន្ធកុំព្យូទ័រនីមួយៗមានទម្រង់ឯកសារខុសៗគ្នា ហើយពិបាកក្នុងការទាញយកព័ត៌មានឬអានឯកសារពីកុំព្យូទ័រផ្សេង។<br /><br />
                    លោក Tim Berners-Lee បានមានគំនិតបង្កើតប្រព័ន្ធមួយ ដែលអាចអោយមនុស្សទូទាំងពិភពលោក អាចចូលមើល និងចែករំលែកព័ត៌មានតាមរយៈបណ្ដាញតែមួយ។ ដូច្នេះហើយ លោកបានបង្កើត៖
                </p>

                <ul style={styles.list}>
                    <List_article Text="World Wide Web" />
                    <List_article Text="HTML សម្រាប់សរសេរឯកសារ" />
                    <List_article Text="HTTP សម្រាប់បញ្ជូនទិន្នន័យ" />
                    <List_article Text="URL សម្រាប់កំណត់អាសយដ្ឋានគេហទំព័រ" />
                </ul>

                <Title_article Title="កំណើត HTML ដំបូង" ID="origin_html" />
                <p style={styles.p}>
                    នៅឆ្នាំ ១៩៨៩ លោក Tim Berners-Lee បានស្នើគម្រោង World Wide Web ជាលើកដំបូង។ បន្ទាប់មកនៅឆ្នាំ ១៩៩១ លោកបានបង្កើត HTML ជាផ្លូវការ។ HTML កំណែដំបូងមាន Tag តិចតួចប៉ុណ្ណោះ ដូចជា៖ &lt;html&gt;, &lt;head&gt;, &lt;body&gt;, &lt;p&gt; ជាដើម។<br /><br />
                    HTML ត្រូវបានរចនាឡើងអោយមានលក្ខណៈងាយស្រួល សាមញ្ញ និងអាចអានបានដោយមនុស្ស។ វាអនុញ្ញាតអោយអ្នកប្រើប្រាស់ចុចលើតំណភ្ជាប់ (Hyperlink) ដើម្បីទៅកាន់ឯកសារផ្សេង។
                </p>

                <Title_article Title="ការអភិវឌ្ឍន៍ HTML បន្ត" ID="development_html" />
                <p style={styles.p}>
                    បន្ទាប់ពី HTML ត្រូវបានប្រើប្រាស់យ៉ាងទូលំទូលាយ អង្គការផ្សេងៗបានចូលរួមអភិវឌ្ឍន៍វាបន្ថែម។ នៅឆ្នាំ ១៩៩៤ បានបង្កើតអង្គការ W3C (World Wide Web Consortium) ដោយ Tim Berners-Lee ផ្ទាល់ ដើម្បីគ្រប់គ្រងស្តង់ដារនៃ HTML និងបច្ចេកវិទ្យា Web ផ្សេង។<br /><br />
                    HTML បានអភិវឌ្ឍតាមកំណែផ្សេង។<br />
                </p>
                <ul style={styles.list}>
                    <List_article Text="HTML 2.0 (1995): កំណែដំបូងដែលបានទទួលស្គាល់ជាស្តង់ដារ។" />
                    <List_article Text="HTML 3.2 (1997): បន្ថែម Tag និងលក្ខណៈថ្មីៗ។" />
                    <List_article Text="HTML 4.01 (1999): បន្ថែមសមត្ថភាពសម្រាប់បង្កើតគេហទំព័រជាច្រើន។" />
                    <List_article Text="XHTML (2000): កំណែដែលរួមបញ្ចូល XML ជាមួយ HTML។" />
                    <List_article Text="HTML5 (2014): កំណែចុងក្រោយដែលមានលក្ខណៈសម្បូរបែប សម្រាប់បង្កើតគេហទំព័រទំនើប។" />
                </ul>

                <Title_article Title="សារសំខាន់នៃ HTML" ID="importance_html" />
                <p style={styles.p}>
                    HTML គឺជាគ្រឹះសំខាន់នៃបណ្ដាញអ៊ីនធឺណិត។ វាអនុញ្ញាតអោយអ្នកបង្កើត និងរៀបចំមាតិកានៅលើគេហទំព័រ។ ដោយគ្មាន HTML អ្នកប្រើប្រាស់នឹងមិនអាចមើលឬចូលដំណើរការគេហទំព័របានឡើយ។<br /><br />
                    HTML ក៏ជាមូលដ្ឋានសម្រាប់បច្ចេកវិទ្យា Web ផ្សេងៗដូចជា CSS (Cascading Style Sheets) សម្រាប់រចនាបទ និង JavaScript សម្រាប់បន្ថែមសមត្ថភាពអន្តរកម្ម។
                </p>

            </section>
        </main-center>
    );
};

export default Main_center;
