import React from 'react';
import { Link } from 'react-router-dom';

import "../../../../../style/Animation/header-global.css";
import HeaderHomepage from '../../Global/HeaderHomepage';
import Nav_Header from './Nav_Header';
import Main_left from './Main_left.jsx';
import Main_center from './Main_center.jsx';
import Main_right from './Main_right.jsx';

const Contact_main = () => {
    return (
        <>
            <section style={{width: '100%', backgroundColor: 'var(--cb-black)', height: '100vh'}}>
                <HeaderHomepage />
                <main style={{width: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                    <div style={{width: '100%', position: 'sticky', top: '60px', height: 'calc(100vh - 50px)', backgroundColor: 'var(--cb-light-black)', display: 'flex', flexDirection: 'column', boxSizing: 'border-box'}}>
                        <Nav_Header />
                        <div style={{width: '100%', height: '100%',display: 'flex', alignItems: 'center', justifyContent: 'space-between'}}>
                            <Main_left />
                            <Main_center />
                            <Main_right />
                        </div>
                    </div>
                </main>
            </section>
        </>
    );
};

export default Contact_main;